import { Component, Host, h, Prop } from '@stencil/core';
import { HorizontalAlignment } from '../../utils/components.model';

@Component({
  tag: 'fle-image-info-tile',
  styleUrl: 'fle-image-info-tile.scss',
  shadow: false, // no shadow dom cause CMSs system need access for live preview
})
export class FleImageInfoTile {
  @Prop() headline: string;
  @Prop() category: string;
  @Prop() description: string;
  @Prop() mediaUrl: string;
  @Prop() mediaAltText: string;
  @Prop() imagePosition: HorizontalAlignment = 'left';

  render() {
    const reverseClass = this.imagePosition === 'right' ? 'reverse' : '';
    return (
      <Host class={`fle-image-info-tile ${reverseClass}`}>
        <fle-media class="media" media-url={this.mediaUrl} media-alt-text={this.mediaAltText}></fle-media>
        <fle-info class="info"
                  headline={this.headline}
                  category={this.category}
                  description={this.description}
        ></fle-info>
        {/*<slot></slot>*/}
      </Host>
    );
  }
}
