:host {
}

.fle-image-info-tile {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;

  @media (min-width: map-get($grid-breakpoints, lg)) {
    max-width: 100%;
    flex-flow: row nowrap;
    padding-left: 15%;
    padding-right: 15%;
    gap: 32px;

    &.reverse {
      flex-direction: row-reverse;
    }
  }

  .media {
    @media (min-width: map-get($grid-breakpoints, lg)) {
      flex: 1 1 0;
    }
  }

  .info {
    background-color: unset;
    color: black;

    @media (min-width: map-get($grid-breakpoints, lg)) {
      flex: 1 1 0;
    }
  }
}
